import React from 'react';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class PolyPipes extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    if (InputValues.BYP_502_Active === undefined || InputValues.BYP_502_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='PolyPipes (1978-1995)' />
        <div className='form_inputs' />
      </>
    );
  }
}
