import React from 'react';
import TextInputer from './TextInputer';
import TextAreaInputer from './TextAreaInputer';
import RadioGroup from './RadioGroup';
import MultipleChoises from './MultipleChoises';
import IntlTelInputer from './IntlTelInputer';
// import CustomSelect from './CustomSelect';
import Select from './Select';
import FileUploader from './FileUploader';
import SingleOption from './SingleOption';
import * as Consts from '../../shared/Consts';

export const GenerateInput = (props) => {
  const {
    FieldDefs,
    FieldName,
    Title,
    AdditionalOptions
  } = props;

  const options = AdditionalOptions === undefined ? [] : AdditionalOptions;

  if (FieldDefs !== undefined && FieldName !== undefined) {
    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);

    if (fieldDef === undefined) {
      return (
        <span style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>ERROR: Field definition for {FieldName} does not exist</span>
      );
    }

    if (fieldDef.Type === Consts.FIELD_TYPE_TEXT ||
      fieldDef.Type === Consts.FIELD_TYPE_EMAIL ||
      fieldDef.Type === Consts.FIELD_TYPE_DATE ||
      fieldDef.Type === Consts.FIELD_TYPE_NUMBER ||
      fieldDef.Type === Consts.FIELD_TYPE_CURRENCY ||
      fieldDef.Type === Consts.FIELD_TYPE_INTEGER
    ) {
      return (
        <TextInputer
          FieldName={FieldName}
          Required={fieldDef.Required}
          MaxLength={fieldDef.MaxLength}
          FieldDef={fieldDef}
          {...props}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_INTL_TEL) {
      let label = '';
      if (Title === undefined) {
        label = fieldDef.Title;
      } else if (Title !== '') {
        label = Title;
      }

      return (
        <IntlTelInputer
          FieldName={FieldName}
          Required={fieldDef.Required}
          Type={fieldDef.Type}
          Label={label}
          FieldDef={fieldDef}
          {...props}
        />);
    } else if (fieldDef.Type === Consts.FIELD_TYPE_TEXTAREA) {
      let label = '';
      if (Title === undefined) {
        label = fieldDef.Title;
      } else if (Title !== '') {
        label = Title;
      }

      return (
        <TextAreaInputer
          FieldName={FieldName}
          Required={fieldDef.Required}
          Type={fieldDef.Type}
          MaxLength={fieldDef.MaxLength}
          Label={label}
          {...props}
        />);
    } else if (fieldDef.Type === Consts.FIELD_TYPE_OPTIONS) {
      return (
        <RadioGroup
          FieldName={FieldName}
          Options={fieldDef.Options.concat(options)}
          {...props}
        />);
    } else if (fieldDef.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES) {
      return (
        <MultipleChoises
          FieldName={FieldName}
          Options={fieldDef.Options.concat(options)}
          {...props}
        />);
    } else if (fieldDef.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
      return (
        <RadioGroup
          FieldName={FieldName}
          Options={[
            { label: 'Yes', value: Consts.OPTION_YES_VALUE },
            { label: 'No', value: Consts.OPTION_NO_VALUE }
          ]}
          {...props}
        />);
    } else if (fieldDef.Type === Consts.FIELD_TYPE_SINGLE_OPTION) {
      return (
        <SingleOption
          FieldName={FieldName}
          {...props}
        />);
    } else if (fieldDef.Type === Consts.FIELD_TYPE_CUSTOME_SELECT) {
      return (
        <Select
          FieldName={FieldName}
          FieldType={fieldDef.Type}
          Options={fieldDef.Options.concat(options)}
          Title={fieldDef.Title}
          {...props}
        />
      );
    } else if (fieldDef.Type === Consts.FIELD_TYPE_MULTIPLE_FILES || fieldDef.Type === Consts.FIELD_TYPE_SINGLE_FILE) {
      const Multiple = (fieldDef.Type === Consts.FIELD_TYPE_MULTIPLE_FILES);
      return (
        <FileUploader
          Multiple={Multiple}
          FieldName={FieldName}
          FieldType={fieldDef.Type}
          {...props}
        />
      );
    }
  } else {
    return (
      <div>Unknown definition</div>
    );
  }
};

export const GetFieldTitle = (props) => {
  const {
    FieldDefs,
    FieldName
  } = props;

  var title = <span style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>ERROR: Field definition for {FieldName} does not exist</span>;
  if (FieldDefs !== undefined && FieldName !== undefined) {
    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
    if (fieldDef !== undefined && fieldDef.Title !== undefined) {
      title = fieldDef.Title;
    }
  }

  return title;
};

export const GetFieldDescription = (props) => {
  const {
    FieldDefs,
    FieldName
  } = props;

  var description;
  if (FieldDefs !== undefined && FieldName !== undefined) {
    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
    if (fieldDef !== undefined && fieldDef.Description !== undefined) {
      description = fieldDef.Description;
    }
  }

  return description;
};

export const GenerateInputerForm = (props) => {
  const {
    FieldDefs,
    FieldName,
    InputValues
  } = props;
  if (FieldDefs !== undefined && FieldName !== undefined) {
    const fieldDef = FieldDefs.find(item => item.FieldName === FieldName);
    if (fieldDef === undefined) {
      return (<span style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>ERROR: Field definition for {FieldName} does not exist</span>);
    } else {
      var toDisplay = true;

      if (fieldDef.DependsOn !== undefined) {
        console.log(typeof fieldDef.DependsOn);
        if (typeof fieldDef.DependsOn === 'object' && Array.isArray(fieldDef.DependsOn) === true) {
          for (var dependsOn of fieldDef.DependsOn) {
            console.log(InputValues[dependsOn.field]);
            if (IsDependsOnMet(InputValues[dependsOn.field], dependsOn.value) === false) {
              toDisplay = false;
              break;
            }
          }
        } else if (typeof fieldDef.DependsOn === 'string') {
          const dependsOnValue = fieldDef.DependsOnValue === undefined ? Consts.OPTION_YES_VALUE : fieldDef.DependsOnValue;
          if (IsDependsOnMet(InputValues[fieldDef.DependsOn], dependsOnValue) === false) {
            toDisplay = false;
          }
        } else {
          toDisplay = false;
        }
      }

      if (toDisplay === true) {
        return (
          <div>
            <div className='inputer_form'>{GenerateInput({ ...props })}</div>
          </div>
        );
      } else {
        return null;
      }
    }
  } else {
    return (<span style={{ color: 'red', fontWeight: 'bold', fontSize: '1.2rem' }}>ERROR: properties not provided.</span>);
  }
};

const IsDependsOnMet = (inputValue, dependsOnValue) => {
  if (inputValue === undefined) {
    return false;
  } else {
    if (typeof inputValue === 'object' && Array.isArray(inputValue)) {
      const inputValueItem = inputValue.find(item => item.value === dependsOnValue);
      return inputValueItem !== undefined;
    } else {
      return inputValue === dependsOnValue;
    }
  }
};
